@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@300;600&display=swap');
* {
  margin: 0;
  padding: 0;
}
body {
  background-color: white;
  color: #222;
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
}
.wrapper {
  
  margin: 50px;
}
header {
  position: relative;
}
header .logo {
  font-weight: 600;
  font-size: 20px
}

header ul.nav {
  float: right;
  list-style: none;
}

header ul.nav li {
  display: inline;
  margin-left: 25px;
  cursor: pointer;
  transition: opacity 500ms ease;
}

header ul.nav li:hover{
  opacity: 0.5;
}

header .shop {
  transform: scale(1.5);
  float: right;
  cursor: pointer;
  transition: color transform 500ms ease;
}
header .shop:hover, header .shop.active {
  color: #bcbcbc;
  transform: scale(1.75);
}

header .shop-cart {
  position: absolute;
  top: 30px;
  right: 0;
  width: 450px;
  background: #fafafa;
  -webkit-box-shadow: 4px 5px 9px -1px #696969; 
  box-shadow: 4px 5px 9px -1px #696969;
  z-index: 100;
  padding: 20px;
  padding-bottom: 0;
}
header .shop-cart .empty h2 {
  font-size: 20px;
  margin-bottom: 20px;
}
header .shop-cart .item {
  width: 100%;
  float: left;
  margin-bottom: 20px;
}
header .shop-cart .item img {
  width: 70px;
  float: left;
  margin-right: 20px;
}

header .shop-cart .item h2 {
  font-size: 20px;
  margin-bottom: 10px;
}

header .shop-cart .item p {
  color:#696969;
  font-weight: 600;
}

header .shop-cart .item .delete {
  float: right;
  position: relative;
  top: -25px;
  cursor: pointer;
  transition: color, transform 500ms ease;
}
header .shop-cart .item .delete:hover {
  color: #bcbcbc;
  transform: scale(1.5);
}

header .shop-cart .sum {
  float: left;
  width: 100%;
  font-weight: 600;
  font-size: 20px;
  margin-bottom: 20px;
}

.presentation {
  margin: 50px auto;
  background: url(./img/Pro.jpg) no-repeat;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center center;
  background-blend-mode: multiply;
  background-color: #bcbcbc;
  position: relative;
}
.presentation::after {
  content: "Niche and luxury fragrances";
  position: absolute;
  top: 60px;
  left: 60px;
  width: 300px;
  font-size: 40px;
  font-weight: 600;
  color: #fff;
}
.presentation::before {
  content: "from top brands";
  position: absolute;
  top: 210px;
  left: 60px;
  width: 300px;
  font-size: 15px;
  font-weight: 300;
  color: #fff;
}
.categories {
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
  display: flex;
}
.categories div {
  text-align: center;
  justify-content: center;
  width: auto;
  background-color: #f2f2f2;
  border-radius: 50px;
  padding: 10px 20px;
  margin: 0 10px 35px;
  cursor: pointer;
  border: 1px solid transparent;
  transition: all 500ms ease;
}
.categories div:hover {
  border-color: silver;
  transform: scale(1.1);
}
.full-item {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(51, 51, 51, 0.703);
  z-index: 9999;
  overflow: auto;
}
.full-item > div {
  width: 60%;
  position: relative;
  margin: 10% auto;
  padding: 40px 30px;
  background: #fff;
  border-radius: 9px;
}
main {
  display: flex;
  width: 100%;
  flex-wrap: wrap;
  justify-content: space-between;
}
main .item {
  width: 30%;
  margin-bottom: 50px;
  background: #f5f5f5;
  overflow: hidden;
  position: relative;
  padding-bottom: 20px;
}
main .item img, .full-item img {
  width: 100%;
  border-radius: 10px 10px 0 0;
  transition: transform 500ms ease;
  cursor: pointer;
}
main .item img:hover, .full-item img:hover {
  transform: scale(1.05);
}
main h2, .full-item h2, main p, .full-item p, main b, .full-item b {
  margin: 10px 20px;
  color: #333;
}
main .add,
.full-item .add {
  position: absolute;
  right: 20px;
  bottom: 20px;
  background: #333;
  color: #f5f5f5;
  width: 35px;
  height: 35px;
  text-align: center;
  line-height: 35px;
  border-radius: 50%;
  cursor: pointer;
  font-weight: 600;
  transition: transform 500ms ease;
}
main .add:hover,
.full-item .add:hover {
  transform: scale(1.5) translateY(-5px);
  
}
footer {
  text-align: center;
  margin-top: 100px;
}

@media screen and (max-width: 768px) {
  main .item {
    width: 45%;
   }
  .categories div {
    width: 30%;
  }
}